@media all and (min-width: 420px) and (max-width: 800px) {
}
@media all and (max-width: 500px) {
    .title {
        font-size: 35px;
        margin-bottom: 25px;
    }
    .text {
        font-size: 18px;
    }
}
@media all and (max-width: 600px) {
    .plan-blocks-block {
        padding: 30px 10px;
        &-content {
            > img:nth-child(1) {
                width: 100%;
            }
        }
    }
    .r_title {
        font-size: 30px;
        &-1 {
            margin-bottom: 10px;
        }
    }
}
@media all and (max-width: 630px) {
    .ready-blocks-block {
        margin: 15px 0 !important;
    }
}
