.footer {
    margin-top: 200px;
    &-content {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        border-top: 2px solid rgba(165, 155, 134, 0.2);
        > div,
        > ul {
            margin-top: 75px;
        }
    }
    &-left {
        max-width: 270px;
    }
    &-center {
        max-width: 230px;
        margin: 0 150px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        a {
            font-size: 19px;
            line-height: 20px;
            color: #65727b;
        }
        &-margin {
            margin: 0 40px;
        }
        li {
            margin-top: 10px;
            margin-bottom: 10px;
        }
    }
    &-nav {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        li {
            a {
                font-size: 13px;
                line-height: 20px;
                text-decoration-line: underline;
                color: #28514f;
            }
        }
    }
    &-margin {
        margin: 0 70px;
        &-2 {
            margin: 0 60px 0 70px;
        }
    }
    &-right {
        &-margin {
            margin: 0 30px;
        }
    }
}
