.header {
    &-container {
        max-width: 1200px;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        min-height: 100px;
        align-items: center;
        flex-wrap: wrap;
    }
    &-list {
        display: flex;
        li {
            margin: 0 30px;
            height: 38px;
            display: flex;
            justify-content: center;
            align-items: center;
            a {
                font-size: 16px;
                line-height: 23px;
                color: #65727b;
            }
        }
    }
}
.burger-click {
    display: none;
}
.page-active {
    background: rgba(101, 114, 123, 0.2);
    padding: 0 20px;
    border-radius: 40px;
}
