@media all and (max-width: 930px) {
    .menu-btn {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 10px;
        width: 50px;
        height: 40px;
        cursor: pointer;
        transition: all 0.5s ease-in-out;
        &-burger {
            width: 40px;
            height: 3px;
            background: #28514f;
            border-radius: 5px;
            transition: all 0.5s ease-in-out;
            &::after,
            &::before {
                content: "";
                position: absolute;
                width: 40px;
                height: 3px;
                background: #28514f;
                border-radius: 5px;
                transition: all 0.5s ease-in-out;
            }
            &::after {
                transform: translateY(11px);
            }
            &::before {
                transform: translateY(-11px);
            }
        }
    }
    .open {
        padding-bottom: 100%;
        .header-right {
            max-height: 1000px;
        }
        .menu-btn-burger {
            transform: translateX(-50px);
            background: transparent;
            &::before {
                transform: rotate(45deg) translate(35px, -35px);
            }
            &::after {
                transform: rotate(-45deg) translate(35px, 35px);
            }
        }
    }
}
