// Подключение своих SCSS файлов
@import "fonts";
@import "header";
.wrapper {
    max-width: 1440px;
    background: #f9f7f4;
    margin: 0 auto;
}
body {
    font-family: "Roboto";
}
ul {
    list-style: none;
    padding: 0;
}
.font {
    &-rb {
        font-family: "Roboto";
    }
    &-dm {
        font-family: "DM Serif Display";
    }
    &-mw {
        font-family: "Merriweather";
    }
    &-sp {
        font-family: "Source Sans Pro";
    }
}
.container {
    margin: 0 auto;
    max-width: 940px;
}
a {
    text-decoration: none;
}
button {
    border: none;
    cursor: pointer;
}
.after-del {
    position: relative;
    &::after {
        content: "";
        display: block;
        background: rgba(27, 31, 43, 0.12);
        border-radius: 40px;
        width: 157px;
        height: 3px;
        position: absolute;
        bottom: -50px;
    }
}
.main {
    &-content {
        margin-top: 120px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        &-top {
            max-width: 650px;
        }
        &-bottom {
            img {
                width: 100%;
            }
        }
    }
}
.title {
    font-family: "DM Serif Display";
    margin: 0 0 38px 0;
    font-style: normal;
    font-weight: normal;
    font-size: 48px;
    line-height: 56px;
    text-align: center;
    color: #28514f;
}
.text {
    font-family: "Source Sans Pro";
    font-style: normal;
    font-weight: normal;
    font-size: 21px;
    line-height: 32px;
    text-align: center;
    color: #65727b;
    margin: 0;
}
.buttons {
    margin-top: 46px;
    display: grid;
    max-width: 340px;
    grid-template-columns: repeat(2, 1fr);
    justify-content: center;
    margin: 46px auto 0;
}
.button {
    font-family: "Source Sans Pro";
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 23px;
    color: #ffffff;
    width: 158px;
    height: 56px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50px;
    &-apple {
        background: #28514f;
        margin-right: 20px;
        &::before {
            content: "";
            display: block;
            width: 24px;
            height: 24px;
            background: url(../img/apple.svg) no-repeat;
            background-size: contain;
            margin-right: 10px;
        }
    }
    &-google {
        background: #aeb4b6;
        &::before {
            content: "";
            display: block;
            width: 24px;
            height: 24px;
            background: url(../img/google.svg) no-repeat;
            background-size: contain;
            margin-right: 10px;
        }
    }
    &-text {
        grid-column: 2/3;
        width: max-content;
        justify-self: center;
        font-family: "Source Sans Pro";
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 20px;
        color: #65727b;
    }
}
.plan {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 250px 0 0 0;
    &-top {
        max-width: 640px;
    }
    &-bottom {
        display: flex;
        flex-wrap: wrap;
        margin-top: 105px;
    }
    &-blocks {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        &-top {
            align-items: flex-end;
        }
        &-bottom {
            align-items: flex-start;
        }
        &-block {
            max-width: 460px;
            border-radius: 20px;
            padding: 60px 50px;
            height: max-content;
            &-content {
                max-width: 360px;
                display: flex;
                flex-wrap: wrap;
            }
            &-1 {
                background: #dae1f4;
                margin: 0 20px 10px 0;
            }
            &-2 {
                background: #d0e6f3;
                margin-bottom: 10px;
            }
            &-3 {
                background: #e0f0e6;
                margin: 0 20px 0px 0;
            }
            &-4 {
                background: #f4f0e9;
            }
        }
    }
    &::after {
        bottom: -200px;
    }
}
.r_title {
    font-family: "Roboto";
    font-style: normal;
    font-weight: bold;
    font-size: 40px;
    line-height: 47px;
    margin: 0;
    color: #1b1f2b;
    &-1 {
        margin: 0 0 58px 0;
    }
}
.t_text {
    font-family: "Roboto";
    font-style: normal;
    font-weight: normal;
    font-size: 17px;
    line-height: 24px;
    color: #1b1f2b;
}
.order {
    &-3 {
        order: 3;
    }
}
.ready {
    margin-top: 300px;
    position: relative;
    &::after {
        content: "";
        display: block;
        background: #008c6e;
        border: 2px solid #008c6e;
        box-sizing: border-box;
        border-radius: 30px;
        bottom: -25px;
        position: absolute;
        width: 25px;
        height: 15px;
        left: 48.4%;
    }
    &-blocks {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        &-block {
            max-width: 300px;
            background: #ffffff;
            border: 1px solid rgba(0, 0, 0, 0.1);
            box-sizing: border-box;
            border-radius: 20px;
            padding: 30px 40px 30px 30px;
            &-text {
                font-family: "Merriweather";
                font-style: normal;
                font-weight: normal;
                font-size: 13px;
                line-height: 20px;
                color: #1b1f2b;
            }
            &-bottom {
                display: flex;
                flex-wrap: wrap;
                align-items: center;
                margin-top: 15px;
                &-left {
                    margin: 0 10px 0 0px;
                }
            }
        }
    }
    &-margin {
        margin: 0 20px;
    }
}
.stars {
    margin: 0;
}
.name {
    font-family: "Source Sans Pro";
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 19px;
    margin: 0;
    color: #1b1f2b;
}
.ready-text {
    font-family: "Source Sans Pro";
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    margin: 0;
    color: #65727b;
}
.mission {
    background: #28514f;
    margin-top: 200px;
    padding: 310px 0;
    position: relative;
    &-content {
        max-width: 575px;
        margin: 0 auto;
    }
    &-title {
        color: #fff;
    }
    &-text {
        color: #ffffff;
    }
    &-img {
        position: absolute;
        &-1 {
            z-index: 1;
            top: -100px;
            left: calc(50% - 173px / 2 + 0.5px);
        }
        &-2 {
            width: 100%;
            bottom: 0;
            left: 0;
        }
    }
    &-button {
        margin: 60px auto 0;
        font-family: "Source Sans Pro";
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 23px;
        color: #28514f;
        background: #fff;
    }
}
.step {
    background: #f8e6be;
    margin-top: 50px;
    padding: 20px;
    box-sizing: border-box;
}
.step-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-end;
    &-left {
        max-width: 400px;
        .title {
            text-align: left;
        }
        .buttons {
            margin-bottom: 20px;
        }
    }
    &-right {
        max-width: 420px;
        img {
            width: 100%;
        }
    }
}
.join {
    margin-top: 200px;
    &-content {
        max-width: 620px;
        margin: 0 auto;
    }
    &-input {
        position: relative;
        max-width: 400px;
        margin: 35px auto 0;
        &-text {
            font-size: 14px;
            line-height: 20px;
            text-align: center;

            color: #65727b;
        }
        input {
            width: 276px;
            height: 60px;
            box-sizing: border-box;
            padding: 18px 30px;
            background: #ffffff;
            box-shadow: 0px 4px 0px rgba(167, 167, 167, 0.16);
            border-radius: 40px 0px 0px 40px;
            border: none;
            font-family: "Roboto";
            font-style: normal;
            font-weight: normal;
            font-size: 20px;
            line-height: 20px;
            color: rgba(40, 81, 79, 0.5);
            &::placeholder {
                font-family: "Roboto";
                font-style: normal;
                font-weight: normal;
                font-size: 20px;
                line-height: 20px;
                color: rgba(40, 81, 79, 0.5);
            }
        }
        button {
            top: 0;
            right: 10px;
            position: absolute;
            background: #28514f;
            box-shadow: 0px 4px 0px rgba(167, 167, 167, 0.16);
            border-radius: 0px 40px 40px 0px;
            width: 117px;
            height: 60px;
            font-size: 17px;
            line-height: 36px;
            text-align: center;
            text-transform: uppercase;
            color: #ffffff;
        }
    }
}
@import "burger";
@import "footer";
@import "adaptive";
